html {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  min-width: 375px;
  min-height: 100%;
  color: #ffffff;
  font-family: "Rubik", "Arial", sans-serif; }

.container {
  margin: 0 auto;
  padding: 0 24px;
  min-width: 327px;
  max-width: 550px; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* Blocks */
.page-main {
  min-height: 100vh; }
  @media (min-width: 768px) {
    .page-main {
      display: grid;
      grid-template-rows: min-content 1fr; } }

.page-main__top {
  position: relative; }

.input-section {
  padding-top: 29px;
  padding-bottom: 157px;
  background-image: url("../img/pattern-bg.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-color: #5163c6; }
  @media (min-width: 768px) {
    .input-section {
      padding-bottom: 80px; } }

.input-section__title {
  margin: 0;
  color: #ffffff;
  text-align: center;
  font-size: 25px;
  line-height: 25px;
  font-weight: 700; }

.info-section {
  z-index: 1000;
  position: absolute;
  left: 24px;
  right: 24px;
  top: 50%;
  margin-top: 17px;
  padding-top: 25px;
  padding-bottom: 25px;
  background-color: #ffffff;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 2px 10px #3f3f3f94; }
  @media (min-width: 768px) {
    .info-section {
      top: 71%; } }
  @media (min-width: 1120px) {
    .info-section {
      left: 50%;
      width: 1110px;
      margin-left: -555px; } }

.search {
  display: flex;
  align-items: center;
  margin-top: 31px;
  background-color: #ffffff;
  border-radius: 15px;
  overflow: hidden; }

.search__text {
  flex-grow: 1;
  padding: 0 24px;
  font-size: 18px;
  background-color: #ffffff;
  border: 0; }

.search__button {
  width: 58px;
  height: 58px;
  background-color: #000000;
  background-image: url("../img/icon-arrow.svg");
  background-repeat: no-repeat;
  background-position: center center;
  border: 0; }
  .search__button:hover, .search__button:focus {
    background-color: #3f3f3f;
    cursor: pointer; }
  .search__button:active {
    opacity: 0.3; }

.search--process .search__button {
  cursor: wait; }

.info-list {
  margin: 0;
  color: #000000;
  text-align: center;
  background-color: #ffffff; }
  @media (min-width: 768px) {
    .info-list {
      display: flex;
      text-align: left; } }

.info-list__title {
  margin-bottom: 9px;
  color: #b4b4b4;
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  text-transform: uppercase; }

.info-list__description {
  margin: 0;
  color: #313131;
  font-size: 21px;
  line-height: 21px;
  font-weight: 400; }

.info-list__item {
  position: relative;
