  margin-bottom: 25px; }
  .info-list__item:last-child {
    margin-bottom: 0; }
  @media (min-width: 768px) {
    .info-list__item {
      flex-grow: 1;
      width: 25%;
      margin-bottom: 0; }
      .info-list__item:not(:last-child)::after {
        content: "";
        position: absolute;
        right: -32px;
        top: 8px;
        bottom: 8px;
        width: 1px;
        background-color: #d8d8d8; } }

.map {
  min-height: 530px; }
  @media (min-width: 768px) {
    .map {
      min-height: auto;
      height: 100%; } }

.error {
  z-index: 100;
  position: absolute;
  top: 8px;
  left: 8px;
  right: 8px;
  display: flex;
  min-height: 40px;
  color: #721c24;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 3px;
  overflow: hidden; }

.error__message {
